.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.5);
    color: black;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}

.activeModal {
    opacity: 1;
    pointer-events: all;
}

.modal_content {
    transform: scale(0.5);
    transition: 0.5s all;
}

.activeContent {
    transform: scale(1);
}