.App {
    width: 500px;
    height: 100%;
    background-color: #EEE;
    padding: 20px 30px 14px 30px;
    box-shadow: 0 0 500px #111;
}

h2, h4 {
    text-align: center;
}

.color-grid-wrapper {
    display: flex;
    justify-content: center;
}

.color-grid {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 150px 150px 150px;
    grid-template-rows: 150px 150px 150px;
}

.mistake {
    margin-top: 10px;
    margin-bottom: -32px;
    height: 22px;
}

.color-container {
    margin: 5px;
    transition: 0.2s;
}

.color-container:hover {
    margin: 20px;
    transition: 0.2s;
    cursor: pointer;
}

.game-over {
    height: 70px;
    width: 150px;
    background-color: white;
    border-radius: 10px;
    text-align: center;
    padding: 40px;
    font-size: 2em;
}
